import { Delete, Edit } from "@material-ui/icons";
import { Button, Col, Empty, Form, Input, message, Modal, Row } from "antd";
import axios from "axios";
import i18next from "i18next";
import React from "react";
import { useState } from "react";
import { useEffect } from "react";
import { useTranslation } from "react-i18next";
import { API_URL, STORAGE_NAME } from "../../utils/constant";

const SocialSetting = ({ social }) => {
    const { t } = useTranslation()
    const [form] = Form.useForm()
    const [item, setItem] = useState({
        open: false,
        edit: false,
        item: null
    });
    const [socialStatuses, setSocialStatuses] = useState([])
    const [refresh,setRefresh]=useState(false)
    let token = localStorage.getItem(STORAGE_NAME);

    useEffect(() => {
        axios.get(API_URL + '/words/social-status').then((res) => {
            console.log(res);
            setSocialStatuses(res?.data?.object);
        })
    }, [refresh])

    const deleteSocial = (item) => {
        axios({
            method: "delete",
            url: API_URL + '/words/social-status',
            data: item,
            headers: {
                Authorization: token
            }
        }).then(res => {
            message.success(t("Social status deleted succesflly!"))
            setItem({
                open:false,edit:false,item:null
            })
            setRefresh(!refresh)
        }).catch(e => {
            message.error(t("Error social status delete!"))
        })
    }


    const onFinish = (values) => {
        console.log(values);
        let method=item?.edit?'put':'post';
        let data={};
        Object.entries(values)?.map(([key,value])=>{
            if(key?.startsWith('name')){
                let a=key?.split("_");
                if(data?.name){
                    data={
                        ...data,
                        name:{
                            ...data?.name,
                            [`${a[1]}`]:value
                        }
                    }
                }else{
                    data={
                        ...data,
                        name:{
                            [`${a[1]}`]:value
                        }
                    }
                }
            }
            if(key?.startsWith('desc')){
                let a=key?.split("_");
                if(data?.description){
                    data={
                        ...data,
                        description:{
                            ...data?.description,
                            [`${a[1]}`]:value
                        }
                    }
                }else{
                    data={
                        ...data,
                        description:{
                            [`${a[1]}`]:value
                        }
                    }
                }
            }
        })

        if
    (item?.edit){
        data={...data,id:item?.item?.id}
    }

        axios({
            method:method,
            data:data,
            url:API_URL+'/words/social-status',
            headers:{
                Authorization:token
            }
        }).then(res=>{
            message.success(item?.edit?t("Social status edited succesflly!"):t("Social status successfully created!"))
            setItem({
                open:false,edit:false,item:null
            })
            setRefresh(!refresh)
        }).catch(e=>{
            message.error(item?.edit?t("Error social status edit!"):t("Error social status create!"))
        })

    };

    return (
        <>
            <h3 style={{ paddingTop: "15px" }}><b>
                {
                    t("Social statuses")
                }
            </b></h3>
            {
                social && <div>
                    <Button className="mt-2 mb-2" onClick={() => setItem({ open: true, edit: false, item: null })} style={{ float: "right" }} type="primary">{t("Add")}</Button>
                    {socialStatuses?.length > 0 ? <table>
                        <thead>
                            <tr>
                                <th className="table-border number">#</th>
                                <th className="table-border name-uz">{t("Name")}</th>
                                <th className="table-border name-ru">{t("Description")}</th>
                                <th style={{ width: "25px" }} className="table-border delete">{t("Action")}</th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                socialStatuses?.map((item, i) =>
                                    <tr>
                                        <td className="table-border ">{i + 1}</td>
                                        <td className="table-border ">{item?.name[i18next.language]}</td>
                                        <td className="table-border ">{item?.description && item?.description[i18next.language]}</td>
                                        <td className="table-border ">
                                            <Button onClick={() => {
                                                setItem({ edit: true, open: true, item: item })
                                                form.setFieldsValue({
                                                    name_uz:item?.name?.uz,
                                                    name_ru:item?.name?.ru,
                                                    name_uzCyr:item?.name?.uzCyr,
                                                    desc_uz:item?.description?item?.description?.uz:null,
                                                    desc_ru:item?.description?item?.description?.ru:null,
                                                    desc_uzCyr:item?.description?item?.description?.uzCyr:null
                                                })
                                            }} type="link">
                                                <Edit />
                                            </Button>
                                            <Button onClick={() => deleteSocial(item)} type="link" danger>
                                                <Delete />
                                            </Button>
                                        </td>
                                    </tr>
                                )
                            }
                        </tbody>
                    </table> :
                        <div>
                            <Empty />
                        </div>}
                </div>
            }
            <Modal title={item?.edit ? <>{t("Edit social status!")}</> : <>{t("Add social status")}</>} footer={false} visible={item?.open} onCancel={() => {
                setItem({ open: false, edit: false, item: null })
                form.resetFields()
            }}>
                <Form
                    layout="vertical"
                    form={form}
                    onFinish={onFinish}
                >
                    <Row gutter={24}>
                        <Col xs={24} sm={24} md={12} lg={12}>
                            <Form.Item
                                rules={[{
                                    required: true,
                                    message: t("Please input field!")
                                }]}
                                name="name_uz"
                                label="Name(uz)"
                            >
                                <Input placeholder="Name(uz)" />
                            </Form.Item>
                        </Col>
                        <Col xs={24} sm={24} md={12} lg={12}>
                            <Form.Item
                                rules={[{
                                    required: true,
                                    message: t("Please input field!")
                                }]}
                                name="desc_uz"
                                label="Description(uz)"
                            >
                                <Input placeholder="Description(uz)" />
                            </Form.Item>
                        </Col>
                        <Col xs={24} sm={24} md={12} lg={12}>
                            <Form.Item
                                rules={[{
                                    required: true,
                                    message: t("Please input field!")
                                }]}
                                name="name_ru"
                                label="Name(ru)"
                            >
                                <Input placeholder="Name(ru)" />
                            </Form.Item>
                        </Col>
                        <Col xs={24} sm={24} md={12} lg={12}>
                            <Form.Item
                                rules={[{
                                    required: true,
                                    message: t("Please input field!")
                                }]}
                                name="desc_ru"
                                label="Description(ru)"
                            >
                                <Input placeholder="Description(ru)" />
                            </Form.Item>
                        </Col>
                        <Col xs={24} sm={24} md={12} lg={12}>
                            <Form.Item
                                rules={[{
                                    required: true,
                                    message: t("Please input field!")
                                }]}
                                name="name_uzCyr"
                                label="Name(uzKrill)"
                            >
                                <Input placeholder="Name(uzKrill)" />
                            </Form.Item>
                        </Col>
                        <Col xs={24} sm={24} md={12} lg={12}>
                            <Form.Item
                                rules={[{
                                    required: true,
                                    message: t("Please input field!")
                                }]}
                                name="desc_uzCyr"
                                label="Description(uzKrill)"
                            >
                                <Input placeholder="Description(uzKrill)" />
                            </Form.Item>
                        </Col>
                        <Col span={24}>
                            <Button style={{float:"right"}} type="primary" htmlType="submit">{t("Submit")}</Button>
                        </Col>
                    </Row>
                </Form>
            </Modal>
        </>
    )
}

export default SocialSetting;