import React, {useEffect, useState} from "react";
import Title from "../Title";
import axios from "axios";
import {Link, withRouter} from 'react-router-dom';
import {API_URL} from "../../utils/constant";
import Swal from "sweetalert2";
import KeyboardBackspaceIcon from "@material-ui/icons/KeyboardBackspace";
import {withTranslation} from "react-i18next";
import NavCenter from "../Nav/NavCenter";
import NavTop from "../Nav/NavTop";
import InputMask from 'react-input-mask'
import i18next from "i18next";
import {Button, Checkbox, Col, DatePicker, Form, Input, Row, Select} from "antd";
import * as moment from "moment";

const {Option} = Select;

const RegistrationApplicant = (props) => {
    const {history, t} = props;
    const [form]=Form.useForm();
    const [regions, setRegions] = useState([]);
    const [districts, setDistricts] = useState([]);
    const [socialStatus, setSocialStatus] = useState([]);
    const [values, setValues] = useState(({
        fullName: '',
        sectionId: '',
        nationId: 1,
        phoneNumber: '',
        email: '',
        districtId: '',
        password: '',
        birthDate: '',
        address: '',
        prePassword: '',
        socialStatusId: '',
        gender: ''
    }))


    useEffect(() => {
        axios.get(API_URL + "/region").then(res => {
            setRegions(res.data)
        });
    }, []);
    useEffect(() => {
        axios.get(API_URL + "/words/social-status").then(res => {
            setSocialStatus(res?.data?.object)
        })
    }, []);

    const fetchDistricts = (id) => {
        axios.get(API_URL + "/region/district?id=" + id + "").then(res => {
            setDistricts(res?.data);
        });
    };

    const onFinish = (values) => {
        if(values?.password===values?.prePassword){
            values={...values,
                phoneNumber: values?.phoneNumber?.replaceAll(" ", "")
                .replaceAll("(", "")
                .replaceAll(")", "")};
            axios.post(API_URL + "/auth/createApplicant", {...values}).then(res => {
                if (res?.data?.success) {
                    Swal.fire({
                        position: 'top-end',
                        icon: 'success',
                        title: props.t(res.data?.object?.message),
                        showConfirmButton: false,
                        timer: 1000
                    }).then(() => {
                        history.push({
                            pathname:'/check-phone-number',
                            state:{
                                phone:values?.phoneNumber
                            }
                        })
                    });
                } else {
                    Swal.fire({
                        position: 'top-end',
                        icon: 'error',
                        title: props.t("An error occurred. Please try again") + "!!!",
                        showConfirmButton: false,
                        timer: 2000
                    }).then((e) => {
                    });
                }
            }).catch((e) => {
                let a=[];
                Object.entries( e?.response?.data).map(([key,value])=>{
                    if(key==="email"){
                        a.push({
                            name:[key],
                            errors:[props.t("Email is already available")]
                        })
                    }
                    if(key==="phoneNumber"){
                        a.push({
                            name:[key],
                            errors:[props.t("The phone number is already available")]
                        })
                    }
                })
                form.setFields(a)
            });
        }else {
            form.setFields([
                {
                    "name":["password"],
                    errors:[t("Passwords did not match")]
                },
                {
                    "name":["prePassword"],
                    errors:[t("Passwords did not match")]
                }
            ])
        }
    };

    return (
        <div>
            <div className="nav">
                <NavTop/>
                <NavCenter/>
                {/*<NavBottom/>*/}
            </div>
            <div className="registration-applicant container-fluit12">
                <div className="container12">
                    <div className="registration-applicant-wrapper">
                        <Title text={<span><KeyboardBackspaceIcon titleAccess="Bosh sahifaga" onClick={() => {
                            history.goBack()
                        }} style={{marginRight: "17px", cursor: "pointer"}}/>{
                            props.t("Register")}
                        </span>}/>
                        <h5>{props.t("Personal data")}</h5>
                        <Form form={form} layout="vertical" onFinish={onFinish}>
                            <Row gutter={24}>
                                <Col xs={24} sm={24} md={12} lg={12} className="form-first">
                                    <Row gutter={24}>
                                        <Col xs={24} sm={24} md={{span: 16, offset: 3}} lg={{span: 16, offset: 3}}>
                                            <Form.Item
                                                style={{width: "100%"}}
                                                name="fullName"
                                                label={props.t("Full name")}
                                                rules={[{
                                                    required: true,
                                                    message: t("Name must be spelled!")
                                                }]}
                                            >
                                                <Input
                                                    
                                                    placeholder={props.t("Enter your full name")}
                                                />
                                            </Form.Item>
                                        </Col>
                                        <Col xs={24} sm={24} md={{span: 16, offset: 3}} lg={{span: 16, offset: 3}}>
                                            <Form.Item rules={[{required: true, message: t("Please select gender!!!")}]}
                                                       name="gender" label={props.t("Gender")}>
                                                <Select style={{height: "100%"}}
                                                        placeholder={props.t("Choose your gender")}>
                                                    <Option value="erkak">{props.t("Male")}</Option>
                                                    <Option value="ayol">{props.t("Female")}</Option>
                                                </Select>
                                            </Form.Item>
                                        </Col>

                                        <Col xs={24} sm={24} md={{span: 16, offset: 3}} lg={{span: 16, offset: 3}}>
                                            <Form.Item
                                                label={props.t("Date of Birth")}
                                                name="birthDate"
                                                rules={[{
                                                    required: true,
                                                    message: t("Please input birth date")
                                                }]}
                                            >
                                                <DatePicker
                                                    placeholder={t("Input birth date")}
                                                    style={{width: "100%"}}
                                                    defaultPickerValue={moment("01-01-2000")}
                                                    format={"DD-MM-YYYY"}
                                                />
                                            </Form.Item>
                                        </Col>

                                        <Col xs={24} sm={24} md={{span: 16, offset: 3}} lg={{span: 16, offset: 3}}>
                                            <Form.Item
                                                name="regionId"
                                                label={props.t("Region")}
                                                rules={[{
                                                    required: true,
                                                    message: t("Please select your region!!!")
                                                }]}
                                            >
                                                <Select
                                                    onChange={(e)=>{
                                                        fetchDistricts(e)
                                                        form.setFieldsValue({
                                                            districtId:undefined,
                                                            address:undefined
                                                        })
                                                    }}
                                                    placeholder={props.t("Select your region")}
                                                    style={{height: "100%"}}
                                                >
                                                    {regions && regions.map((item) =>
                                                        <Option key={item.id}
                                                                value={item.id}>{item.name[i18next.language]}</Option>
                                                    )}
                                                </Select>
                                            </Form.Item>
                                        </Col>
                                        <Col xs={24} sm={24} md={{span: 16, offset: 3}} lg={{span: 16, offset: 3}}>
                                            <Form.Item
                                                name="districtId"
                                                label={props.t("City (region)")}
                                                rules={[{
                                                    required: true,
                                                    message: t("Please choose your district")
                                                }]}
                                            >
                                                <Select
                                                    style={{height: "100%"}}
                                                    placeholder={props.t("Choose your district")}
                                                >
                                                    {districts && districts.map((item, i) =>
                                                        <Option key={i}
                                                                value={item.id}>{item.name[i18next.language]}</Option>
                                                    )}
                                                </Select>
                                            </Form.Item>
                                        </Col>
                                        <Col xs={24} sm={24} md={{span: 16, offset: 3}} lg={{span: 16, offset: 3}}>
                                            <Form.Item
                                                name="address"
                                                label={props.t("Home address")}
                                                rules={[{
                                                    required: true,
                                                    message: t("Please enter your home address")
                                                }]}
                                            >
                                                <Input.TextArea
                                                    placeholder={props.t("Enter your home address")}
                                                />
                                            </Form.Item>
                                        </Col>
                                    </Row>
                                </Col>

                                <Col sm={24} xs={24} md={12} lg={12} className="form-last">
                                    <Row gutter={24}>
                                        <Col xs={24} sm={24} md={{span: 16, offset: 3}} lg={{span: 16, offset: 3}}>
                                            <Form.Item
                                                name="phoneNumber"
                                                label={props.t("Telephone")}
                                                rules={[{
                                                    required: true,
                                                    message: t("Please input your phone")
                                                }]}
                                            >
                                                <InputMask
                                                    mask="+\9\9\8(99)999 99 99"
                                                    style={{
                                                        // boxShadow: "0px 0px 15px rgb(0 0 0 / 10%)",
                                                        boxSizing: "border-box",
                                                        margin: 0,
                                                        fontVariant: "tabular-nums",
                                                        listStyle: "none",
                                                        fontFeatureSettings: "tnum",
                                                        position: "relative",
                                                        display: "inline-block",
                                                        width: "100%",
                                                        minWidth: 0,
                                                        padding: "4px 11px",
                                                        color: "rgba(0, 0, 0, 0.85)",
                                                        fontSize: "14px",
                                                        lineHeight: 1.5715,
                                                        backgroundColor: "#fff",
                                                        backgroundImage: "none",
                                                        border: "1px solid #d9d9d9",
                                                        borderRadius: "2px",
                                                        transition: " all 0.3s"
                                                    }}
                                                    placeholder="+998 (__) ___-__-__"/>
                                            </Form.Item>
                                        </Col>
                                        <Col xs={24} sm={24} md={{span: 16, offset: 3}} lg={{span: 16, offset: 3}}>
                                            <Form.Item
                                                name="email"
                                                label={props.t("Email")}
                                                rules={[{
                                                    required: true,
                                                    message: t("Please input your email")
                                                }]}
                                            >
                                                <Input
                                                    type="email"
                                                    placeholder={props.t("Enter your mail")}
                                                />
                                            </Form.Item>

                                        </Col>
                                        <Col xs={24} sm={24} md={{span: 16, offset: 3}} lg={{span: 16, offset: 3}}>
                                            <Form.Item
                                                label={props.t("Benefit category")}
                                                name="socialStatusId"
                                                rules={[{
                                                    required: true,
                                                    message: t("Please select your benefits")
                                                }]}
                                            >
                                                <Select
                                                    style={{height: "100%"}}
                                                    placeholder={props.t("Select benefits")}
                                                >
                                                    {socialStatus && socialStatus.map((item, i) =>
                                                        <Option key={i}
                                                                value={item.id}>{item.name[i18next.language]}</Option>
                                                    )}
                                                </Select>
                                            </Form.Item>
                                        </Col>
                                        <Col xs={24} sm={24} md={{span: 16, offset: 3}} lg={{span: 16, offset: 3}}>
                                            <Form.Item
                                                name="password"
                                                label={props.t("Password")}
                                                rules={[{
                                                    required: true,
                                                    message: t("Please input password")
                                                }]}
                                            >
                                                <Input.Password
                                                    autoComplete="new-password"
                                                    placeholder={props.t("Enter your password")}
                                                />
                                            </Form.Item>
                                        </Col>
                                        <Col xs={24} sm={24} md={{span: 16, offset: 3}} lg={{span: 16, offset: 3}}>
                                            <Form.Item
                                                name="prePassword"
                                                label={props.t("Repeat password")}
                                                rules={[{
                                                    required: true,
                                                    message: t("Please input re-password")
                                                }]}
                                            >
                                                <Input.Password type="password"
                                                    placeholder={props.t("Re-enter your password")}/>
                                            </Form.Item>
                                        </Col>
                                        <Col xs={24} sm={24} md={{span: 16, offset: 3}} lg={{span: 16, offset: 3}}>
                                            <Checkbox>
                                                {props.t("I consent to the processing of my personal data and have read the ")}
                                            </Checkbox>
                                        </Col>
                                        <Col className="mt-2" xs={24} sm={24} md={{span: 16, offset: 3}}
                                             lg={{span: 16, offset: 3}}>
                                            <Button style={{
                                                float: "right",
                                                boxShadow: "0px 0px 15px rgba(0, 0, 0, 0.1)",
                                                borderRadius: "15px"
                                            }} htmlType="submit" type="primary"
                                                   >{props.t("Registration")}</Button>
                                        </Col>
                                    </Row>
                                </Col>

                            </Row>
                        </Form>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default withTranslation()(withRouter(RegistrationApplicant));
