import React from "react";
import {Button, Col, Form, Input, message, Row} from 'antd'
import NavTop from "../Nav/NavTop";
import NavCenter from "../Nav/NavCenter";
import Title from "../Title";
import KeyboardBackspaceIcon from "@material-ui/icons/KeyboardBackspace";
import {useHistory,useLocation} from 'react-router-dom'
import {useTranslation} from "react-i18next";
import {useSelector} from "react-redux";
import axios from "axios";
import {API_URL} from "../../utils/constant";
import Swal from "sweetalert2";

const CheckPhone=({phone})=>{
    const [form]=Form.useForm();
    const theme=useSelector(state => state.theme);
    const {t}=useTranslation();
    const location=useLocation();
    const history=useHistory();

    console.log(location);

    const onFinish=(values)=>{
        console.log(values);
        values={...values,phone:location?.state?.phone}
        console.log(values)
        axios.post(API_URL+'/auth/check-phone',values,{
            headers:{
                'Access-Control-Allow-Origin':'*'
            }
        }).then(resp=>{
            Swal.fire({
                position: 'top-end',
                icon: 'success',
                title: t("User successfully registered!"),
                showConfirmButton: false,
                timer: 1000
            }).then(() => {
                history.push({
                    pathname: '/auth/login'
                })
            });
        })
    };
    
    const reSendCode=()=>{
        Swal.fire({
            title:t("Re send secret code!"),
            showCancelButton:true,
            cancelButtonText:t("No!"),
            confirmButtonText:t("Yes!"),
            cancelButtonColor:"red",
            icon:"warning"
        }).then(conf=>{
            if(conf.isConfirmed){
                axios.get(API_URL+'/auth/re-send-secret-code',{params:{phone:location?.state?.phone}}).then(res=>{
                    message.success(t("Secret code re send"));
                }).catch(e=>{
                    message.error(t("Error for re send code!"))
                })
            }
        })
    }

    return(
        <div>
            <div className="nav">
                <NavTop/>
                <NavCenter/>
            </div>
            <div className="login container-fluit12">
                <div style={theme} className="container12">
                    <div className="login-wrapper">

                        <Title text={<span style={{cursor: "pointer"}} onClick={() => {
                            history.push("/")
                        }}><KeyboardBackspaceIcon titleAccess="Bosh sahifaga"
                                                  style={{marginRight: "17px", cursor: "pointer"}}/>{

                            t("Your secret code!")}
                        </span>}/>
                        <div className="form">
                        <h3>{t("We send secret code this number! Number: ")+location?.state?.phone}</h3>
                            <Form layout="vertical" form={form} onFinish={onFinish}>
                               
                                <Row gutter={24}>
                                    <Col span={24}>
                                        <Form.Item
                                            name="code"
                                            label="Secret code"
                                            rules={[
                                                {
                                                    required:true,
                                                    message:t("Please input secret code!")
                                                }
                                            ]}
                                        >
                                            <Input placeholder={t("Input your secret code")}/>
                                        </Form.Item>
                                    </Col>
                                    <Col span={24}>
                                        <Button type="link" onClick={reSendCode}>
                                            {
                                                t("Send again")
                                            }
                                        </Button>
                                        <Button style={{float:"right"}} type="primary" htmlType="submit">{t("Send")}</Button>
                                    </Col>
                                </Row>
                            </Form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
};

export default CheckPhone;