import React, { useEffect, useState } from "react";
import Title from "../Title";
import axios from "axios";
import { API_URL } from "../../utils/constant";
import Swal from "sweetalert2";
import { withTranslation } from "react-i18next";
import KeyboardBackspaceIcon from "@material-ui/icons/KeyboardBackspace";
import NavTop from "../Nav/NavTop";
import NavCenter from "../Nav/NavCenter";
import InputMask from "react-input-mask";
import i18next from "i18next";
import { Form, Input, Select, DatePicker, Row, Col, Button } from 'antd'
import moment from "moment";

const { Item } = Form;
const { Option } = Select

function RegistrationListener(props) {
    const { history, t } = props;
    const [form] = Form.useForm();
    const [regions, setRegions] = useState([]);
    const [districts, setDistricts] = useState([]);
    const [sections, setSections] = useState([]);
    useEffect(() => {
        axios.get(API_URL + "/region").then(res => {
            setRegions(res.data)
        });
    }, []);
    useEffect(() => {
        axios.get(API_URL + "/section").then(res => {
            setSections(res.data);
        })
    }, []);

    const fetchDistricts = (e) => {
        let id = e

        axios.get(API_URL + "/region/district?id=" + id + "").then(res => {
            setDistricts(res.data);
        })
    };

    // const handleSend = (e) => {
    //     // console.log("values")
    //     // console.log(values)
    //     e.preventDefault();
    //     axios.post(API_URL + "/auth/createListener", { ...values }).then(res => {
    //         if (res.data.success) {
    //             Swal.fire({
    //                 position: 'top-end',
    //                 icon: 'success',
    //                 title: t("Registered") + "!!!",
    //                 showConfirmButton: false,
    //                 timer: 1000
    //             }).then(() => {
    //                 history.push("/auth/login")
    //             });
    //         } else {
    //             Swal.fire({
    //                 position: 'top-end',
    //                 icon: 'error',
    //                 title: t("An error occurred. Please try again") + "!!!",
    //                 showConfirmButton: false,
    //                 timer: 2000
    //             }).then(() => {
    //             });
    //         }
    //     }).catch((e) => {
    //         // console.log("e.response.data")
    //         // console.log(e.response.data)
    //         Swal.fire({
    //             position: 'top-end',
    //             icon: 'error',
    //             title: e.response.data.phoneNumber && e.response.data.phoneNumber ?
    //                 props.t("The phone number and email are already available") :
    //                 e.response.data.phoneNumber ?
    //                     props.t("The phone number is already available") : e.response.data.email ?
    //                         props.t("Email is already available") : props.t("An error occurred. Please try again") + "!",
    //             showConfirmButton: false,
    //             timer: 2000
    //         }).then(() => {
    //             // console.log(e.response)
    //         });
    //     });
    // }



    const onFinish = (values) => {
        console.log(values);
        values={...values,
            phoneNumber: values?.phoneNumber?.replaceAll(" ", "")
            .replaceAll("(", "")
            .replaceAll(")", "")};
        axios.post(API_URL + "/auth/createListener", values).then(res => {
            if (res.data.success) {
                Swal.fire({
                    position: 'top-end',
                    icon: 'success',
                    title: t("Registered") + "!!!",
                    showConfirmButton: false,
                    timer: 1000
                }).then(() => {
                    history.push("/auth/login")
                });
            } else {
                Swal.fire({
                    position: 'top-end',
                    icon: 'error',
                    title: t("An error occurred. Please try again") + "!!!",
                    showConfirmButton: false,
                    timer: 2000
                }).then(() => {
                });
            }
        }).catch((e) => {
            // console.log("e.response.data")
            // console.log(e.response.data)
            Swal.fire({
                position: 'top-end',
                icon: 'error',
                title: e.response.data.phoneNumber && e.response.data.phoneNumber ?
                    props.t("The phone number and email are already available") :
                    e.response.data.phoneNumber ?
                        props.t("The phone number is already available") : e.response.data.email ?
                            props.t("Email is already available") : props.t("An error occurred. Please try again") + "!",
                showConfirmButton: false,
                timer: 2000
            }).then(() => {
                // console.log(e.response)
            });
        });

    }

    return (
        <div className="registration-listnear container-fluit12">
            <div className="nav">
                <NavTop />
                <NavCenter />
                {/*<NavBottom/>*/}
            </div>
            <div className="container12">
                <div className="registration-listnear-wrapper">
                    <Title text={<span><KeyboardBackspaceIcon titleAccess="Bosh sahifaga" onClick={() => {
                        history.goBack()
                    }} style={{ marginRight: "17px", cursor: "pointer" }} />{

                            props.t("Register")}
                    </span>} />
                    <h5>{props.t("Personal data")}</h5>
                    <Form onFinish={onFinish} layout="vertical" form={form}>
                        <Row gutter={24}>
                            <Col xs={24} sm={24} md={12} lg={12}>
                                <Row gutter={24}>
                                    <Col span={18} offset={3}>
                                        <Item
                                            label={props.t("Full name")}
                                            name="fullName"
                                            rules={[
                                                {
                                                    required: true,
                                                    message: t("Please input your full name!")
                                                }
                                            ]}
                                        >
                                            <Input
                                                 
                                                type="text"
                                                placeholder={props.t("Enter your full name")}
                                            />
                                        </Item>
                                    </Col>
                                    <Col span={18} offset={3}>
                                        <Item
                                            label={props.t("Date of birth")}
                                            name="birthDate"
                                            rules={[
                                                {
                                                    required: true,
                                                    message: t("Please input your date of birth!")
                                                }
                                            ]}>
                                            <DatePicker
                                                style={{ width: "100%" }}
                                                defaultPickerValue={moment('01-01-2000')}
                                                format={"DD-MM-YYYY"}
                                            />
                                        </Item>
                                    </Col>
                                    <Col span={18} offset={3}>
                                        <Item
                                            label={props.t("Region")}
                                            name="regionId"
                                            rules={[
                                                {
                                                    required: true,
                                                    message: t("Please input your region!")
                                                }
                                            ]}>
                                            <Select style={{ height: "100%" }} placeholder={props.t("Select your region")} onChange={fetchDistricts}>
                                                {regions && regions.map((item, i) =>
                                                    <Option key={i} value={item.id}>{item.name[i18next.language]}</Option>
                                                )}
                                            </Select>
                                        </Item>
                                    </Col>
                                    <Col span={18} offset={3}>
                                        <Item
                                            label={props.t("Region (city)")}
                                            name="districtId"
                                            rules={[
                                                {
                                                    required: true,
                                                    message: t("Please input your district!")
                                                }
                                            ]}>
                                            <Select style={{ height: "100%" }} placeholder={props.t("Choose your district")}>
                                                {districts && districts.map((item, i) =>
                                                    <Option key={i} value={item.id}>{item.name[i18next.language]}</Option>
                                                )}
                                            </Select>
                                        </Item>
                                    </Col>
                                    <Col span={18} offset={3}>
                                        <Item
                                            label={props.t("Home address")}
                                            name="address"
                                            rules={[
                                                {
                                                    required: true,
                                                    message: t("Please input your address!")
                                                }
                                            ]}
                                        >
                                            <Input
                                                 
                                                placeholder={props.t("Enter your home address")}
                                            />
                                        </Item>
                                    </Col>

                                </Row>
                            </Col>
                            <Col xs={24} sm={24} md={12} lg={12}>
                                <Row gutter={24}>
                                    <Col span={18} offset={3}>
                                        <Item
                                            label={props.t("Department")}
                                            name="sectionId"
                                            rules={[
                                                {
                                                    required: true,
                                                    message: t("Please input your department!")
                                                }
                                            ]}
                                        >
                                            <Select style={{ height: "100%" }} placeholder={props.t("Choose your department")}>
                                                {sections && sections.map((item, i) =>
                                                    <Option key={i} value={item.id}>{item.title[i18next.language]}</Option>
                                                )}
                                            </Select>
                                        </Item>
                                    </Col>
                                    <Col span={18} offset={3}>
                                        <Item
                                            label={props.t("Telephone")}
                                            name="phoneNumber"
                                            rules={[{
                                                required: true,
                                                message: t("Please input your phone!")
                                            }]}
                                        >
                                            <InputMask
                                                style={{
                                                    // boxShadow: "0px 0px 15px rgb(0 0 0 / 10%)",
                                                    boxSizing: "border-box",
                                                    margin: 0,
                                                    fontVariant: "tabular-nums",
                                                    listStyle: "none",
                                                    fontFeatureSettings: "tnum",
                                                    position: "relative",
                                                    display: "inline-block",
                                                    width: "100%",
                                                    minWidth: 0,
                                                    padding: "4px 11px",
                                                    color: "rgba(0, 0, 0, 0.85)",
                                                    fontSize: "14px",
                                                    lineHeight: 1.5715,
                                                    backgroundColor: "#fff",
                                                    backgroundImage: "none",
                                                    border: "1px solid #d9d9d9",
                                                    borderRadius: "2px",
                                                    transition: " all 0.3s"
                                                }}
                                                mask="+\9\9\8(99)999 99 99"
                                                  type="text"
                                                placeholder="+998 (__) ___-__-__" />
                                        </Item>
                                    </Col>
                                    <Col span={18} offset={3}>
                                        <Item
                                            label={props.t("Email")}
                                            name="email"
                                            rules={[{
                                                required: true,
                                                message: t("Please input your email")
                                            }]}
                                        >
                                            <Input
                                                 
                                                type="email"
                                                placeholder={props.t("Enter your mail")}
                                            />
                                        </Item>
                                    </Col>
                                    <Col span={18} offset={3}>
                                        <Item
                                            label={props.t("Password")}
                                            name="password"
                                            rules={[{
                                                required: true,
                                                message: t("Please input your password")
                                            }]}>
                                            <Input.Password
                                                onChange={() => {
                                                    if (form.getFieldValue("prePassword")?.length > 0 && form.getFieldValue("prePassword") !== form.getFieldValue("password")) {
                                                        form.setFields([
                                                            {
                                                                name: ["password"],
                                                                errors: [t("The password did not match")]
                                                            }
                                                        ])
                                                    } else {
                                                        form.setFields([
                                                            {
                                                                name: ["password"],
                                                                validating: true,
                                                                errors: [t("")]
                                                            }
                                                        ])
                                                    }
                                                }}
                                                onBlur={() => {
                                                    if (form.getFieldValue("password").length < 8) {
                                                        form.setFields([
                                                            {
                                                                name: ["password"],
                                                                errors: [t("The password must be at least 8 characters long")]
                                                            }
                                                        ])
                                                    } else {
                                                        form.setFields([
                                                            {
                                                                name: ["password"],
                                                                validating: true,
                                                                errors: [t("")]
                                                            }
                                                        ])
                                                    }
                                                }}
                                                autoComplete="new-password"
                                                  type="password"
                                                placeholder={props.t("Enter your password")}
                                            />
                                        </Item>
                                    </Col>
                                    <Col span={18} offset={3}>
                                        <Item
                                            label={props.t("Repeat password")}
                                            name="prePassword"
                                            rules={[{
                                                required: true,
                                                message: t("Please input your email")
                                            }]}>
                                            <Input.Password
                                                 
                                                placeholder={props.t("Re-enter your password")}
                                            />
                                        </Item>

                                    </Col>
                                    <Col span={18} offset={3} >
                                        <Button style={{ float: "right" }} type="submit"
                                            type="primary" htmlType="submit">{props.t("Registration")}</Button>
                                    </Col>
                                </Row>
                            </Col>
                        </Row>
                    </Form>
                </div>
            </div>
        </div>
    );
}

export default withTranslation()(RegistrationListener);
